import invariant from "invariant";
import constants from "../constants";

export default class Store {
    constructor(id, onDestroy) {
        invariant(id, "Store must have an id");
        invariant(onDestroy instanceof Function, "Store must have an onDestroy function");

        this.__listeners = [];
        this.__onDestroy = onDestroy;
        this.__destroyed = false;
        this.id = id;
    }

    handleMessage(payload) {
        throw new Error("Not implemented");
    }

    _change() {
        this.__listeners.forEach(l => l.call(null));
    }

    addChangeListener(listener) {
        invariant(!this.__destroyed, "This store was already destroyed");
        this.__listeners.push(listener);
    }

    removeChangeListener(listener) {
        const listenerIndex = this.__listeners.indexOf(listener);
        invariant(listenerIndex > -1, "Listener %s is not registered in the store", listener);
        this.__listeners.splice(listenerIndex, 1);
        if (this.__listeners.length === 0) {
            this.__onDestroy.call();
            this.__destroyed = true;
        }
    }
}


