import React from "react";
import Client from "./Client";
import ComponentRegister from "./lib/ComponentRegister";
import dataActionCreator from "./data/dataActionCreator";
import storeProvider from "./data/storeProvider";

var componentRegister = new ComponentRegister();
componentRegister.register("com.mendix.platform.ComponentError", require("./components/ComponentError"));
componentRegister.register("com.mendix.platform.ListView", require("./components/ListView"));

export default new Client(componentRegister, storeProvider, dataActionCreator);


