import React from "react";
import constants from "../constants";
import EntityComponent from "../lib/EntityComponent";

class ListView extends EntityComponent {
    _getDataFromStore(store) {
        return store.state !== constants.storeState.fetched
            ? { message: "Status is " + store.state }
            : { message: null, length: store.data.length };
    }

    render() {
        return this.state.message
            ? React.createElement("p", { style: { color: "red" } }, this.state.message)
            : React.createElement("p", {}, "Bonjour mes amis " + this.state.length);
    }
}

ListView.propTypes = Object.assign({}, EntityComponent.propTypes, {});

export default ListView;


