import invariant from "invariant";
import constants from "../constants";
import Store from "../lib/Store";

export default class DatasourceStore extends Store {
    constructor(id, params, onDestroy) {
        invariant(params && params.type, "Required store parameters are not defined");
        super(id, onDestroy);

        this._data = [];
        this._state = constants.storeState.uninitialized;
        this.params = params;
    }

    get data() {
        return this._data;
    }

    get state() {
        return this._state;
    }

    handleMessage(payload) {
        if (payload.event === constants.event.dataRequested && payload.storeId === this.id) {
            this._state = constants.storeState.requested;
            this._change();
            return;
        }

        if (payload.event === constants.event.dataFetched && payload.storeId === this.id) {
            this._data = payload.data;
            this._state = constants.storeState.fetched;
            this._change();
            return;
        }

        if (payload.event === constants.event.dataReleased && payload.storeId === this.id) {
            this._data = [];
            this._state = constants.storeState.uninitialized;
            this._change();
            return;
        }
    }
}


