import constants from "../constants";
import dispatcher from "../dispatcher";

export default {
    fetch: function(storeId) {
        let store = dispatcher.getStore(storeId);

        let dataLoading = window.mendix.next.dataLayer.loadData(store.params);

        dispatcher.dispatch({
            storeId: storeId,
            event: constants.event.dataRequested
        });

        dataLoading.then(data => dispatcher.dispatch({
            storeId: storeId,
            event: constants.event.dataFetched,
            data: data
        }));
    },
    free: function(storeId) {
        let store = dispatcher.getStore(storeId);

        if (store.state === constants.storeState.fetched) {
            window.mendix.next.dataLayer.release(store.data);
        }

        dispatcher.dispatch({
            storeId: storeId,
            event: constants.event.dataReleased
        });
    }
}


