import React from "react";
import constants from "../constants";

class EntityComponent extends React.Component {
    constructor(props, context) {
        super(props, context);
        this.__changeListener = () => this.setState(this._getDataFromStore(this.__getStore()));
    }

    _getDataFromStore(store) {
        throw new Error("Not implemented");
    }

    componentWillMount() {
        let store = this.__getStore();
        store.addChangeListener(this.__changeListener);
        if (store.state === constants.storeState.uninitialized) {
            this.context.actionCreator.fetch(store.id);
        }
        this.setState(this._getDataFromStore(store));
    }

    componentWillUnmount() {
        let store = this.__getStore();
        if (store.state !== constants.storeState.uninitialized) {
            this.context.actionCreator.free(store.id);
        }
        store.removeChangeListener(this.__changeListener);
    }

    __getStore() {
        return this.context.storeProvider.getStore(this.props.id, this.props.dataSource);
    }
}

EntityComponent.propTypes = {
    id: React.PropTypes.string.isRequired,
    mxid: React.PropTypes.string.isRequired,
    dataSource: React.PropTypes.object.isRequired
};

EntityComponent.contextTypes = {
    storeProvider: React.PropTypes.object.isRequired,
    actionCreator: React.PropTypes.object.isRequired
};

export default EntityComponent;


