import React from "react";
import ComponentError from "./components/ComponentError";

class ElementWrapper extends React.Component {
    getChildContext() {
        return this.props.context;
    }

    render() {
        return React.createElement(this.props.component, this.props.props);
    }
}

ElementWrapper.childContextTypes = {
    storeProvider: React.PropTypes.object.isRequired,
    actionCreator: React.PropTypes.object.isRequired
};

export default class Client {
    constructor(componentRegister, storeProvider, actionCreator) {
        this.__componentRegister = componentRegister;
        this.__storeProvider = storeProvider;
        this.__actionCreator = actionCreator;
    }

    mountComponent(domNode, type, props) {
        if (!this.__componentRegister.has(type)) {
            React.render(
                React.createElement(ComponentError, { name: type }),
                domNode);
            return;
        }

        let component = React.createElement(ElementWrapper, {
            component: this.__componentRegister.get(type),
            context: {
                storeProvider: this.__storeProvider,
                actionCreator: this.__actionCreator
            },
            props: props
        });

        React.render(component, domNode);
    }

    unmountComponent(domNode) {
        React.unmountComponentAtNode(domNode);
    }
}



