import invariant from "invariant";

export default class ComponentRegister {
    constructor() {
        this.__components = {};
    }

    has(name) {
        return this.__components.hasOwnProperty(name);
    }

    get(name) {
        invariant(this.has(name), "Component %s is not registered", name);
        return this.__components[name];
    }

    register(name, component) {
        invariant(!this.has(name), "Component %s is already registered", name);
        this.__components[name] = component;
    }
}


