import flux from "flux";
import invariant from "invariant";
import Store from "./lib/Store";

const fluxDispatcher = new flux.Dispatcher(),
    registeredStores = {};

const dispatcher = {
    register: store => {
        invariant(store instanceof Store, "Provided object %s does not inherit from Store",
            store);
        invariant(!dispatcher.hasStore(store.id), "Store %s has already been registered", store.id);

        const registration = fluxDispatcher.register(store.handleMessage.bind(store));
        registeredStores[store.id] = { store: store, registration: registration };
    },
    unregister: store => {
        invariant(dispatcher.hasStore(store.id), "Store %s is not registered", store.id);

        fluxDispatcher.unregister(registeredStores[store.id].registration);
        delete registeredStores[store.id];
    },
    hasStore: storeId => registeredStores.hasOwnProperty(storeId),
    getStore: storeId => {
        invariant(dispatcher.hasStore(storeId), "Store %s is not registered", storeId);
        return registeredStores[storeId].store;
    },
    dispatch: fluxDispatcher.dispatch.bind(fluxDispatcher),
    waitFor: fluxDispatcher.waitFor.bind(fluxDispatcher)
};

export default dispatcher;


