import DatasourceStore from "./DatasourceStore";
import dispatcher from "../dispatcher";

export default {
    getStore: function(storeId, params) {
        if (dispatcher.hasStore(storeId)) {
            return dispatcher.getStore(storeId);
        }
        const store = new DatasourceStore(storeId, params,
            () => dispatcher.unregister(store));
        dispatcher.register(store);
        return store;
    }
};



